.what-we-do-section-page-left ul li .flip-box {
    perspective: 1000px;
    /* Remove this if you don't want the 3D effect */
}

/* This container is needed to position the front and back side */
.what-we-do-section-page-left ul li .flip-box-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
}

/* Do an horizontal flip when you move the mouse over the flip box container */
.what-we-do-section-page-left ul li .flip-box:hover .flip-box-inner {
    transform: rotateY(180deg);
}

/* Position the front and back side */
.what-we-do-section-page-left ul li .flip-box-front,
.flip-box-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    /* Safari */
    backface-visibility: hidden;
}

/* Style the front side (fallback if image is missing) */
.what-we-do-section-page-left ul li .flip-box-front {
    cursor: pointer;
    position: relative;
}

/* Style the back side */
.what-we-do-section-page-left ul li .flip-box-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    background: #fff;
    transform: rotateY(180deg);
    top: 0px;
    display: grid;
    align-items: center;

}

